@import '~styles/_variables';
@import '~styles/mixins';
.ui-icon-button {
    display: flex;
    align-items: center;
    font-size: inherit;
    @include themify($themes) {
        color: themed('color-primary');
    }
}
[class^='icon-']:before,
[class*=' icon-']:before {
    display: inline-block;
    width: 1em;
    margin: 0 0.2em;
    font-weight: normal;
    font-family: $font-icon;
    font-style: normal;
    font-variant: normal;
    line-height: 1em;
    text-align: center;
    text-transform: none;
    text-decoration: inherit;
}

.icon-user:before {
    content: '\e800';
}
.icon-user-o:before {
    content: '\f2c0';
}
